import React from "react"
import Header from "../components/Header"
import styled from "styled-components"
import AniLink from "gatsby-plugin-transition-link/AniLink"

import "../components/layout.css"

const AboutSection = styled.section`
  text-align: center;
  margin: 50px 0;
`
const AboutTitle = styled.h1`
  font-size: 70px;
  margin-bottom: 10px;
  @media (min-width: 768px) and (max-width: 1024px) {
    font-size: 64px;
  }
  @media (min-width: 200px) and (max-width: 767px) {
    font-size: 46px;
  }
`

const AboutSubtitle = styled.p`
  font-size: 22px;
  max-width: 850px;
  margin: 22px auto;
  letter-spacing: 1px;
  line-height: 42px;
  @media (min-width: 768px) and (max-width: 1024px) {
    font-size: 18px;
  }

  @media (min-width: 200px) and (max-width: 767px) {
    font-size: 16px;
    line-height: 30px;
  }
`
const AboutButton = styled.button`
  font-family: "Nunito", sans-serif;
  width: 300px;
  cursor: pointer;
  color: #fff;
  border: none;
  outline: none;
  height: 65px;
  margin-top: 22px;
  background-color: #252d4f;
  font-size: 16px;
  -webkit-box-shadow: -2px 0px 20px 4px rgb(0 0 0 / 25%);
  box-shadow: -2px 0px 20px 4px rgb(0 0 0 / 25%);
  @media (min-width: 768px) and (max-width: 1024px) {
    width: 250px;
    height: 55px;
  }

  @media (min-width: 200px) and (max-width: 767px) {
    width: 250px;
    height: 55px;
  }
`

const about = () => {
  return (
    <>
      <Header />
      <div className="wrapper">
        <AboutSection>
          <AboutTitle>About Me</AboutTitle>
          <AboutSubtitle>
            Hello, my name is Michael Diggs. I'm a web developer who spends most
            of my days coding, designing, or learning more about the web. For
            over a year now, I've been cultivating and improving my skills to
            produce beautiful and professional work.
          </AboutSubtitle>
          <AniLink paintDrip duration={0.4} hex="#212840" to="/projects/">
            <AboutButton>View My Projects</AboutButton>
          </AniLink>
        </AboutSection>
      </div>
    </>
  )
}

export default about
